#projects{
    margin-top: 6rem;
}
.project-contents{
    padding: 3rem 2rem;
}
.project-content-card{
  margin: 1rem 1rem;
  place-items: center;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 2fr));
  grid-gap: 3.5rem;
  padding: 2rem 2rem;
}
.project-card{
  width: 100%;
  border: 2px solid rgb(var(--primary-text-color));
  padding: 1rem 1rem;
  border-radius: 0.7rem;
  justify-content: space-evenly;
}
.project-image{
  display: block;
  width: 100%;
  border-radius: 0.7rem;
  box-shadow: 0 0 15px 0 rgb(var(--primary-text-color));
}
.project-name {
    color:rgb(var(--primary-text-color));
    text-align: left;
    font-size: 1.5rem;
    padding: 1rem;
    letter-spacing: 0.1rem;
  }
.project-description{
    color:rgb(var(--primary-text-color));
    text-align: left;
    font-size: 1rem;
    padding: 1rem;
    letter-spacing: 0.1rem;   
}
.project-link{
    padding: 1rem 0.5rem;
}
.project-code-link,.project-demo-link{
    color:rgb(var(--primary-text-color));
    text-align: left;
    cursor: pointer;
    padding: 0 0.5rem;
    transition: all 0.2s ease-in-out;
}
.project-code-link:hover, .project-demo-link:hover{
    color: rgb(var(--secondary-text-color));
} 