#skills{
    margin-top: 6rem;
}
.skills-container {
    display: flex;
    align-items: center;
    padding: 3rem 2rem;
  }
  .skills-container-content{
      font-size: 1rem;
  }
  .skills-container>* {
    padding: 1rem 1rem;
  }

  .skill-container{
    border: 2px solid rgb(var(--primary-text-color));
    border-radius: 10px;
    padding: 1rem 2rem;
    margin: 0.7rem 0.7rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: all 0.2s ease-in-out;
  }
  .skill-container:hover{
    transform: scale(1.2);
    cursor: pointer;
    border: 2px solid rgb(var(--secondary-text-color));;
    color: rgb(var(--secondary-text-color));
  }
  .skill-container-logo {
    height: 40px;
  }
  .skill-container-name{
    margin-top: 0.8rem;
  }
  @media  (max-width: 380px){
    .skill-container{
     border: 2px solid rgb(var(--primary-text-color));
    border-radius: 10px;
    padding: 0.5rem 1rem;
    margin: 0.5rem 0.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: all 0.2s ease-in-out;
    }
  }